/**
 * WordPress dependencies
 */
import apiFetch from '@wordpress/api-fetch';
import { dispatch, select } from '@wordpress/data';

/**
 * Internal dependencies
 */
import { STORE_NAME as NAB_DATA } from '../../config';

/**
 * Migrates the given experiment to Nelio A/B Testing 5.0 format.
 *
 * @param {number} experimentId the experiment to migrate.
 */
export async function migrateOldExperiment(
	experimentId: number
): Promise< void > {
	try {
		await apiFetch( {
			path: `/nab/v1/migrate/experiment/${ experimentId }`,
			method: 'PUT',
		} );
	} catch ( e ) {
		console.error( e ); // eslint-disable-line
	} //end if

	const migratedExperiments =
		select( NAB_DATA ).getPageAttribute( 'welcome/migratedExperiments' ) ??
		[];
	dispatch( NAB_DATA ).setPageAttribute( 'welcome/migratedExperiments', [
		...migratedExperiments,
		experimentId,
	] );
} //end migrateOldExperiment()

/**
 * Removes old account data.
 */
export async function removeOldAccountData(): Promise< void > {
	try {
		await apiFetch( {
			path: '/nab/v1/migrate/remove-old-account-data',
			method: 'PUT',
		} );
	} catch ( e ) {
		console.error( e ); // eslint-disable-line
	} //end try
} //end removeOldAccountData()
