/**
 * External dependencies
 */
import { find } from 'lodash';
import type {
	Account,
	AccountMode,
	FastSpringProduct,
	Invoice,
	License,
	Maybe,
	Site,
	SiteId,
	SubscriptionId,
	SubscriptionPlan,
	SubscriptionPeriod,
	SubscriptionState,
} from '@nab/types';

/**
 * Internal dependencies
 */
import { State } from '../types';

export function getAccount( state: State ): Account {
	return state.account.info;
} //end getAccount()

export function getSites(
	state: State,
	id?: SubscriptionId
): ReadonlyArray< Site > {
	if ( ! id ) {
		return [];
	} //end if
	return state.account.sites;
} //end getSites()

export function getSite( state: State, id: SiteId ): Maybe< Site > {
	return find( state.account.sites, { id } );
} //end getSites()

export function getInvoices(
	state: State,
	id: SubscriptionId
): ReadonlyArray< Invoice > {
	if ( ! id ) {
		return [];
	} //end if
	return state.account.invoices;
} //end getInvoices()

export function getSubscriptionId( state: State ): SubscriptionId {
	return state.account.info.subscription;
} //end getSubscriptionId()

export function isSubscriptionDeprecated( state: State ): boolean {
	return !! state.account.info.subscription.startsWith( 'OL' );
} //end isSubscriptionDeprecated()

export function getFullname( state: State ): string {
	return state.account.info.fullname;
} //end getFullname()

export function getFirstname( state: State ): string {
	return state.account.info.firstname;
} //end getFirstname()

export function getLastname( state: State ): string {
	return state.account.info.lastname;
} //end getLastname()

export function getEmail( state: State ): string {
	return state.account.info.email;
} //end getEmail()

export function getStartDate( state: State ): string {
	return state.account.info.startDate;
} //end getStartDate()

export function getSubscriptionProduct(
	state: State
): FastSpringProduct[ 'id' ] {
	return state.account.info.productId;
} //end getSubscriptionProduct()

export function getPlan( state: State ): 'free' | SubscriptionPlan {
	return state.account.info.plan;
} //end getPlan()

export function getLicense( state: State ): License {
	return state.account.info.license;
} //end getLicense()

export function getPhoto( state: State ): string {
	return state.account.info.photo;
} //end getPhoto()

export function getProductDisplay( state: State ): string {
	return state.account.info.productDisplay;
} //end getProductDisplay()

export function getMode( state: State ): AccountMode {
	return state.account.info.mode;
} //end getMode()

export function getPeriod( state: State ): SubscriptionPeriod {
	return state.account.info.period;
} //end getPeriod()

export function getState( state: State ): SubscriptionState {
	return state.account.info.state;
} //end getState()

export function getDeactivationDate( state: State ): string {
	return state.account.info.deactivationDate;
} //end getDeactivationDate()

export function getNextChargeDate( state: State ): string {
	return state.account.info.nextChargeDate;
} //end getNextChargeDate()

export function getNextChargeTotal( state: State ): string {
	return state.account.info.nextChargeTotal;
} //end getNextChargeTotal()

export function getSitesAllowed( state: State ): number {
	return state.account.info.sitesAllowed;
} //end getSitesAllowed()

export function getCurrency( state: State ): string {
	return state.account.info.currency;
} //end getCurrency()

export function getQuota( state: State ): number {
	return state.account.info.quota;
} //end getQuota()

export function getQuotaPerMonth( state: State ): number {
	return state.account.info.quotaPerMonth;
} //end getQuotaPerMonth()

export function getQuotaExtra( state: State ): number {
	return state.account.info.quotaExtra;
} //end getQuotaExtra()

export function getProducts(
	state: State
): ReadonlyArray< FastSpringProduct > {
	return state.account.products;
} //end getProducts()

export function getUrlToManagePayments( state: State ): string {
	return state.account.info.urlToManagePayments;
} //end getUrlToManagePayments()

export function isAgency( state: State ): boolean {
	return !! state.account.info.isAgency;
} //end isAgency()
