/**
 * WordPress dependencies
 */
import apiFetch from '@wordpress/api-fetch';
import { dispatch, resolveSelect } from '@wordpress/data';

/**
 * External dependencies
 */
import type {
	Account,
	FastSpringProduct,
	Invoice,
	Site,
	SubscriptionId,
} from '@nab/types';

/**
 * Internal dependencies
 */
import { STORE_NAME as NAB_DATA } from '../../config';

export async function getAccount(): Promise< void > {
	try {
		const account = await apiFetch< Account >( { path: '/nab/v1/site' } );
		dispatch( NAB_DATA ).receiveAccount( account );
	} catch ( error ) {
		const message = error instanceof Error ? error.message : 'unknown';
		// eslint-disable-next-line
		console.warn( `Error while accessing site info. Error: ${ message }` );
	} //end try
} //end getAccount()

export async function getQuota(): Promise< void > {
	await resolveSelect( NAB_DATA ).getAccount();
} //end getQuota()

export async function getSites( id: SubscriptionId ): Promise< void > {
	try {
		const sites = await apiFetch< ReadonlyArray< Site > >( {
			path: `/nab/v1/subscription/${ id }/sites`,
		} );
		dispatch( NAB_DATA ).receiveSites( sites );
	} catch ( error ) {
		const message = error instanceof Error ? error.message : 'unknown';
		// eslint-disable-next-line
		console.warn(
			`Error while accessing sites in subscription ${ id }. Error: ${ message }`
		);
	} //end try
} //end getSites()

