/**
 * External dependencies
 */
import type {
	Account,
	AlternativeId,
	ConfettiType,
	ConfettiOption,
	EntityId,
	EntityInstance,
	EntityKind,
	EntityKindName,
	Experiment,
	ExperimentId,
	FastSpringProduct,
	GoalId,
	HeatmapDataStatus,
	HeatmapMode,
	HeatmapResolution,
	HeatmapResolutionChangeMode,
	Invoice,
	Menu,
	MenuId,
	PluginSettings,
	Results,
	SegmentId,
	Site,
	SiteId,
	Template,
	Theme,
	ThemeId,
	WooCommerceOrderStatus,
} from '@nab/types';

export type State = {
	readonly account: {
		readonly info: Account;
		readonly invoices: ReadonlyArray< Invoice >;
		readonly products: ReadonlyArray< FastSpringProduct >;
		readonly sites: ReadonlyArray< Site >;
	};

	readonly entities: {
		readonly config: ReadonlyArray< EntityKind >;
		readonly data: Record<
			EntityKindName,
			Record< EntityId, EntityInstance >
		>;
	};

	readonly experiments: Record< ExperimentId, Experiment >;

	readonly page: PageAttributes;

	readonly results: Record< ExperimentId, Results >;

	readonly settings: {
		readonly today: string;
		readonly menus: Record< MenuId, Menu >;
		readonly nelio: PluginSettings;
		readonly plugins: ReadonlyArray< string >;
		readonly templates: Record< EntityKindName, ReadonlyArray< Template > >;
		readonly themes: Record< ThemeId, Theme >;
		readonly woocommerce: WooCommerceSettings;
	};
};

export type WooCommerceSettings = {
	readonly orderStatuses: ReadonlyArray< WooCommerceOrderStatus >;
	readonly currencySymbol: string;
};

// ============
// HELPER TYPES
// ============

type PageAttributes = {
	readonly isLocked: boolean;
	readonly sidebarDimensions: {
		readonly top: number;
		readonly height: string;
		readonly applyFix: boolean;
	};

	readonly 'account/usesFullViewAgency'?: boolean;

	readonly 'css-preview/areCssControlsVisible'?: boolean;
	readonly 'css-preview/currentCssPreviewResolution'?:
		| 'desktop'
		| 'tablet'
		| 'mobile';
	readonly 'css-selector/cssSelectorFinderState'?: SelectorFinderState;
	readonly 'css-selector/openedCssSelectorFinderId'?: number | string;

	readonly 'editor/activeExperiment'?: ExperimentId;
	readonly 'editor/activeGoal'?: GoalId;
	readonly 'editor/activeSegment'?: SegmentId;
	readonly 'editor/alternativeBeingApplied'?: AlternativeId | false;
	readonly 'editor/alternativeInPreviewDialog'?: AlternativeId | false;
	readonly 'editor/isExperimentBeingPaused'?: boolean;
	readonly 'editor/isExperimentBeingStopped'?: boolean;

	readonly 'heatmap/activeAlternative'?: AlternativeId;
	readonly 'heatmap/activeFilter'?: ConfettiType;
	readonly 'heatmap/activeMode'?: HeatmapMode;
	readonly 'heatmap/activeResolution'?: HeatmapResolution;
	readonly 'heatmap/awsAuthToken'?: string;
	readonly 'heatmap/confettiFilterOptions'?: Partial<
		Record< ConfettiType, ReadonlyArray< ConfettiOption > >
	>;
	readonly 'heatmap/dataStatus'?: HeatmapDataStatus;
	readonly 'heatmap/disabledFilterOptions'?: ReadonlyArray< string >;
	readonly 'heatmap/firstDayOfWeek'?: number;
	readonly 'heatmap/iframeContentHeight'?: number;
	readonly 'heatmap/iframeContentScale'?: number;
	readonly 'heatmap/intensity'?: number;
	readonly 'heatmap/opacity'?: number;
	readonly 'heatmap/resolutionChangeMode'?: HeatmapResolutionChangeMode;
	readonly 'heatmap/siteId'?: SiteId;
	readonly 'heatmap/timezone'?: string;

	readonly 'menus/isConfirmationDialogForMenuDuplicationVisible'?: boolean;
	readonly 'menus/isDuplicatingMenu'?: boolean;

	readonly 'welcome/areExperimentsBeingMigrated'?: boolean;
	readonly 'welcome/migratedExperiments'?: ReadonlyArray< number >;
	readonly 'welcome/isBackupWarningAccepted'?: boolean;
	readonly 'welcome/isPluginBeingInitialized'?: boolean;
	readonly 'welcome/isPolicyAccepted'?: boolean;

	readonly 'widgets/isConfirmationDialogForWidgetDuplicationVisible'?: boolean;
	readonly 'widgets/isDuplicatingWidgets'?: boolean;
};

type SelectorFinderState = {
	readonly mode: 'normal' | 'explore';
	readonly currentUrl?: string;
	readonly alternativeId: string;
	readonly value: string;
};
