/**
 * External dependencies
 */
import { get } from 'lodash';
import type { Goal, GoalId, Experiment, ExperimentId, Maybe } from '@nab/types';

/**
 * Internal dependencies
 */
import { State } from '../types';

export function getExperiment(
	state: State,
	key: ExperimentId
): Maybe< Experiment > {
	return get( state.experiments, key );
} //end getExperiment()

export type GetExperimentAttributeFunction = < K extends keyof Experiment >(
	key: ExperimentId,
	attribute: K
) => Maybe< Experiment[ K ] >;

export function getExperimentAttribute< K extends keyof Experiment >(
	state: State,
	key: ExperimentId,
	attribute: K
): Maybe< Experiment[ K ] > {
	const experiment = getExperiment( state, key );
	if ( ! experiment ) {
		return;
	} //end if
	return experiment[ attribute ];
} //end getExperimentAttribute()

export function getDefaultGoal(
	state: State,
	key: ExperimentId
): Maybe< Goal > {
	const experiment = getExperiment( state, key );
	if ( ! experiment ) {
		return;
	} //end if
	return experiment.goals[ 0 ];
} //end getDefaultGoal()

export function isWooCommerceGoal(
	state: State,
	key: ExperimentId,
	goalId?: GoalId
): boolean {
	const experiment = getExperiment( state, key );
	if ( ! experiment ) {
		return false;
	} //end if

	goalId = goalId ?? getDefaultGoal( state, key )?.id;
	const goal = experiment.goals.find( ( g ) => g.id === goalId );
	if ( ! goal ) {
		return false;
	} //end if

	const actions = goal?.conversionActions ?? [];
	return (
		0 < actions.length &&
		actions.every( ( { type } ) => type === 'nab/wc-order' )
	);
} //end isWooCommerceGoal()
