/**
 * WordPress dependencies
 */
import apiFetch from '@wordpress/api-fetch';
import { dispatch, select } from '@wordpress/data';
/**
 * External dependencies
 */
import { createErrorNotice } from '@nab/utils';
import type { SiteId, SubscriptionPlan } from '@nab/types';

/**
 * Internal dependencies
 */
import { STORE_NAME as NAB_DATA } from '../../config';

/**
 * Creates a free site
 */
export async function createFreeSite(): Promise< void > {
	try {
		await apiFetch( {
			path: '/nab/v1/site/free',
			method: 'POST',
		} );
	} catch ( error ) {
		createErrorNotice( error );
	} //end try
} //end createFreeSite()

/**
 * Creates a site and connects it with a premium subscription
 */
export async function createPremiumSite(): Promise< void > {
	try {
		await apiFetch( {
			path: '/nab/v1/site/premium',
			method: 'POST',
		} );
	} catch ( error ) {
		createErrorNotice( error );
	} //end try
} //end createPremiumSite()

/**
 * Connects the site with a subscription and reloads the page.
 *
 * @param {string} license Subscription license.
 */
export async function linkSite( license: string ): Promise< void > {
	try {
		dispatch( NAB_DATA ).lock();

		await apiFetch( {
			path: `/nab/v1/site/subscription`,
			method: 'POST',
			data: { license },
		} );

		window.location.reload();
	} catch ( error ) {
		dispatch( NAB_DATA ).unlock();
		createErrorNotice( error );
	} //end try
} //end linkSite()

/**
 * Returns an action object used in signalling that a site is unlinked for the account.
 * If the site is the current one, reload the page instead of returning an action.
 *
 * @param {string} id Site id.
 */
export async function unlinkSite( id: SiteId ): Promise< void > {
	try {
		dispatch( NAB_DATA ).lock();

		await apiFetch( {
			path: `/nab/v1/site/${ id }/subscription`,
			method: 'DELETE',
		} );

		const site = select( NAB_DATA ).getSite( id );

		if ( site?.isCurrentSite ) {
			window.location.reload();
			return;
		} //end if

		dispatch( NAB_DATA ).unlock();
		dispatch( NAB_DATA ).removeSiteAfterUnlink( id );
	} catch ( error ) {
		dispatch( NAB_DATA ).unlock();
		createErrorNotice( error );
	}
} //end unlinkSite()

/**
 * Cancels the subscription and reloads the page.
 */
export async function cancelSubscription(): Promise< void > {
	try {
		dispatch( NAB_DATA ).lock();

		const subscriptionId = select( NAB_DATA ).getSubscriptionId();
		await apiFetch( {
			path: `/nab/v1/subscription/${ subscriptionId }`,
			method: 'DELETE',
		} );

		window.location.reload();
	} catch ( error ) {
		dispatch( NAB_DATA ).unlock();
		createErrorNotice( error );
	}
} //end cancelSubscription()

/**
 * Uncancels the subscription and reloads the page.
 */
export async function uncancelSubscription(): Promise< void > {
	try {
		dispatch( NAB_DATA ).lock();

		const subscriptionId = select( NAB_DATA ).getSubscriptionId();
		await apiFetch( {
			path: `/nab/v1/subscription/${ subscriptionId }/uncancel`,
			method: 'POST',
		} );

		window.location.reload();
	} catch ( error ) {
		dispatch( NAB_DATA ).unlock();
		createErrorNotice( error );
	}
} //end uncancelSubscription()

export async function upgradeSubscription(
	plan: SubscriptionPlan
): Promise< void > {
	try {
		dispatch( NAB_DATA ).lock();

		const subscriptionId = select( NAB_DATA ).getSubscriptionId();
		await apiFetch( {
			path: `/nab/v1/subscription/${ subscriptionId }`,
			method: 'PUT',
			data: { plan },
		} );

		window.location.reload();
	} catch ( error ) {
		dispatch( NAB_DATA ).unlock();
		createErrorNotice( error );
	}
} //end upgradeSubscription()

export async function purchaseQuota(
	quantity: number,
	currency: string
): Promise< void > {
	try {
		dispatch( NAB_DATA ).lock();

		const subscriptionId = select( NAB_DATA ).getSubscriptionId();
		await apiFetch( {
			path: `/nab/v1/subscription/${ subscriptionId }/quota`,
			method: 'POST',
			data: {
				quantity,
				currency,
			},
		} );

		window.location.reload();
	} catch ( error ) {
		dispatch( NAB_DATA ).unlock();
		createErrorNotice( error );
	}
} //end purchaseQuota()
