/**
 * External dependencies
 */
import { reject } from 'lodash';
import type { AnyAction } from '@nab/types';

/**
 * Internal dependencies
 */
import { INIT_STATE } from '../config';
import type { State as FullState } from '../types';
import type { AccountAction as Action } from '../actions/account';

type State = FullState[ 'account' ];

export function account(
	state = INIT_STATE.account,
	action: AnyAction
): State {
	return actualReducer( state, action as Action ) ?? state;
} //end account()

function actualReducer( state: State, action: Action ): State {
	switch ( action.type ) {
		case 'RECEIVE_ACCOUNT':
			return {
				...state,
				info: action.account,
			};

		case 'RECEIVE_INVOICES':
			return {
				...state,
				invoices: action.invoices,
			};

		case 'RECEIVE_PRODUCTS':
			return {
				...state,
				products: action.products,
			};

		case 'RECEIVE_SITES':
			return {
				...state,
				sites: action.sites,
			};

		case 'UNLINK_SITE':
			return {
				...state,
				sites: reject( state.sites, { id: action.id } ),
			};
	} //end switch
} //end actualReducer()
